<script>
let displayAbout = "display: none";
let displayDatenschutz = "display: none";


const showAbout = () => {
  displayAbout = "display: block";
  displayDatenschutz = "display: none";
}

const showDatenschutz = () => {
  displayAbout = "display: none";
  displayDatenschutz = "display: block";
}

const closeButtonHandler = () => {
  displayAbout = "display: none";
  displayDatenschutz = "display: none";
}

</script>


<nav>
  <ul>
    <li><a href="#about" title="Über" on:click|preventDefault={showAbout}>Über</a></li>
    <li><a href="#datenschutz" title="Datenschutz" on:click|preventDefault={showDatenschutz}>Datenschutz</a></li>
  </ul>
</nav>

<div id="about" style={displayAbout}>
  <button type="button" class="close" on:click={closeButtonHandler}>X</button>
  <h1>Über Meine-Bim.com</h1>
  <p>
  Lernprojekt entwickelt mit Svelte und Sass. Daten kommen von der Wiener Linien Api. Derzeit noch mit JSON Placeholder, da die API
  erst Ende 2019 veröffentlicht wird. 
  </p>
</div>

<div id="datenschutz" style={displayDatenschutz}>
  <button type="button" class="close" on:click={closeButtonHandler}>X</button>
  <h1>Datenschutz Erklärung</h1>
  <p>
  Der Schutz Ihrer persönlichen Daten ist uns ein besonderes Anliegen. Wir verarbeiten Ihre Daten daher ausschließlich auf Grundlage der gesetzlichen Bestimmungen (DSGVO, TKG 2003). In diesen Datenschutzinformationen informieren wir Sie über die wichtigsten Aspekte der Datenverarbeitung im Rahmen unserer Website. 
  <br/><br/>
  Beim Besuch unserer Webseite wird Ihre IP-Adresse, Beginn und Ende der Sitzung für die Dauer dieser Sitzung erfasst. Dies ist technisch bedingt und stellt damit ein berechtigtes Interesse iSv Art 6 Abs 1 lit f DSGVO dar. Soweit im Folgenden nichts anderes geregelt wird, werden diese Daten von uns nicht weiterverarbeitet.
  </p>

  <h2>Cookies</h2>
  <p>
  Unsere Website verwendet so genannte Cookies. Dabei handelt es sich um kleine Textdateien, die mit Hilfe des Browsers auf Ihrem Endgerät abgelegt werden. Sie richten keinen Schaden an. Wir nutzen Cookies dazu, unser Angebot nutzerfreundlich zu gestalten. Einige Cookies bleiben auf Ihrem Endgerät gespeichert, bis Sie diese löschen. Sie ermöglichen es uns, Ihren Browser beim nächsten Besuch wiederzuerkennen. Wenn Sie dies nicht wünschen, so können Sie Ihren Browser so einrichten, dass er Sie über das Setzen von Cookies informiert und Sie dies nur im Einzelfall erlauben. Bei der Deaktivierung von Cookies kann die Funktionalität unserer Website eingeschränkt sein.
  </p>
</div>


